    --hs-line-height-max: 1.1;
    --hs-font-size-min: 2.75;
    --hs-font-size-max: 4.5;
    --hs-vf-wght-multiplier-s: 1;
    --hs-vf-wght-multiplier-m: 1;
    --hs-vf-wght-multiplier-l: 1;
    // H1 variables
